/* =====================================================
   HomePage.css – visual styles for Andrew B. Nimely site
   (Hero, YouTube section, Carousel, Cards, Responsiveness)
   ===================================================== */

:root {
  --nimely-blue: #073763;
  --nimely-light: #f0f4ff;
  --nav-bg: rgba(255, 255, 255, 0.9);
  --nav-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}
/************ 1. Hero Section ************/


.hero {
  background: var(--accent-color);
  background-size: cover;
  background-position: center;
  color: var(--primary-color);
  padding: 4rem 0;
  min-height: calc(100vh - 76px);
  display: flex;
  align-items: center;
}

.hero img {
  border: 5px solid #ffffff;
  transition: transform .3s ease;
  max-width: 100%;
  height: auto;
}

.hero img:hover { transform: scale(1.02); }

.hero h1 { color: var(--primary-color); margin-bottom: 1rem; }
.hero h2 { color: var(--dark-color); opacity: .9; }
.hero .lead { font-size: 1.25rem; margin-bottom: 2rem; }

.hero .btn-outline-light {
  color: var(--primary-color);
  border-color: var(--primary-color);
}
.hero .btn-outline-light:hover {
  background-color: var(--primary-color);
  color: var(--light-color);
}

/************ 2. Priority Cards ************/
.card {
  border: none;
  transition: transform .3s ease, box-shadow .3s ease;
  box-shadow: 0 4px 6px rgba(0,0,0,.1);
}
.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0,0,0,.15);
}
.card-title { color: var(--primary-color); margin-bottom: 1rem; }
.card-text  { color: var(--dark-color);  opacity: .9; }

/************ 3. Responsive Hero tweaks ************/
@media (max-width: 768px) {
  .hero { text-align: center; padding: 3rem 0; }
  .hero img { margin-top: 2rem; max-width: 80%; margin-left: auto; margin-right: auto; }
  .hero .btn { display: block; width: 100%; margin-bottom: 1rem; }
  .hero .btn-lg { margin-right: 0 !important; }
}

/************ 4. YouTube Video Section ************/
.video-section { background: #f7f7f7; padding: 3rem 1rem; }
.video-section h2 { margin-bottom: 2rem; }

.video-wrapper {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  overflow: hidden;
}
.video-wrapper iframe {
  position: absolute; top: 0; left: 0; width: 100%; height: 100%; border: 0;
}
@media (max-width: 768px) {
  .video-section { padding: 2rem 1rem; }
  .video-wrapper { max-width: 100%; padding-bottom: 56.25%; }
}

