.carousel-wrapper {
  /* width rules */
  width: 92%;
  max-width: 1300px;

  /* NEW: maintain consistent 16:9 frame instead of fixed vh */
  aspect-ratio: 16 / 9;
  /* If a browser doesn’t support aspect-ratio it’ll fall back to auto-height */

  margin: 0 auto;
  position: relative;
  overflow: hidden;
  border-radius: 1.5rem;
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.18);
}

.carousel-slide {
  display: none;
  transition: opacity 0.6s ease;
  position: absolute;
  inset: 0; /* stretch to wrapper */
}
.carousel-slide.active {
  display: block;
}
.carousel-slide img,
.carousel-slide video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 1rem;
  box-shadow: 0 6px 24px rgba(0,0,0,.25);
}

/* --- Nav Arrows --- */
/* --- Pagination Dots --- */


/* ========== SECTION WRAPPER ========== */
.media-carousel {
  background: var(--nimely-light);
  padding: 4rem 0 5rem;
  text-align: center;
}

.media-carousel h2 {
  font-size: clamp(2rem, 4vw, 3rem);
  margin-bottom: 2rem;
  color: #111;
}

.media-carousel2 {
  background: var(--nimely-blue);
  padding: 4rem 0 5rem;
  text-align: center;
}

.media-carousel2 h2 {
  font-size: clamp(2rem, 4vw, 3rem);
  margin-bottom: 2rem;
  color: #FFF;
}

/* ========== CAROUSEL CORE ========== */


.carousel-slide {
  width: 100%;
  height: 60vh;             /* BIGGER height */
  max-height: 700px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  inset: 0;
  opacity: 0;
  transform: scale(.92);
  transition: opacity .6s ease, transform .6s ease;
}

.carousel-slide.active {
  opacity: 1;
  transform: scale(1);
  z-index: 1;
}

/* Media inside slide */
.carousel-slide img,
.carousel-slide video {
  width: 100%;
  height: 100%;
  object-fit: cover;   /* fill / crop */
  border-radius: 1.5rem;
}

/* ========== NAVIGATION ARROWS ========== */
.nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 56px;             /* bigger tap target */
  height: 56px;
  border-radius: 50%;
  background: var(--nav-bg);
  box-shadow: var(--nav-shadow);
  border: none;
  cursor: pointer;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background .25s ease, transform .25s ease;
}

.nav.prev { left: 1rem; }
.nav.next { right: 1rem; }

.nav:hover {
  background: #fff;
  transform: translateY(-50%) scale(1.05);
}

.nav svg,
.nav span { /* fallback if using span for arrow */
  font-size: 1.8rem;
  color: var(--nimely-blue);
  pointer-events: none;
}

/* ========== DOTS PAGINATION ========== */
.carousel-dots {
  margin-top: 1.75rem;
  display: flex;
  gap: .5rem;
  justify-content: center;
}

.carousel-dots button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #c9d1e6;
  border: none;
  cursor: pointer;
  transition: transform .2s ease, background .2s ease;
}

.carousel-dots button.active {
  background: var(--nimely-blue);
  transform: scale(1.3);
}

/* ========== RESPONSIVE TWEAKS ========== */
@media (max-width: 768px) {
  .carousel-slide {
    height: 45vh;           /* less height on phones */
  }
  .nav {
    width: 48px;
    height: 48px;
  }
}
